body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('background01.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% 100%;
}

button {
  background-color: #dfdc8d;
  padding: 4px;
  min-width: 100px;
  min-height: 30px;
}

button:focus {
  background-color: #5c5908d5;
  color: #ffffff;
}

input {
  color: white;
}

label {
  font-size: 15px;
}

.main {
  padding: 15px;
}

.move-list-item {
  background-color: rgb(43, 23, 1, 0.8);
  padding: 2px;
  margin-bottom: 5px;
  border-radius: 5px;
}

.move-list-item span {
  color: #dfdc8d;
}

.contact-preview {
  display: grid;
  /* grid-template-rows: repeat(auto-fill, minmax(50px, 1fr)); */
  grid-template-columns: repeat(auto-fill, minmax(160px, 2fr));
  margin-bottom: 4px;
}

.user-msg, .statistic-page, .signup-page, .contact-details, .contact-edit {
  /* background-color: rgba(112, 24, 2, 0.952); */
  /* background-color: rgba(70, 12, 12, 0.781); */
  background-color: rgba(43, 23, 1, 0.5);
  padding: 30px;
}

.user-msg img {
  height: 100px;
  border-radius: 50%;
}

.user-msg section, .contact-details-main {
  display: grid;
  grid-template-columns: repeat(2, fit-content(60%));
}

.user-msg h4, .contact-details-main h4 {
  margin-top: 45px;
  margin-left: 15px;
}

.contact-filter {
  padding: 5px;
}

.nav-bar a {
  color: #dfdc8d;
}